@charset "UTF-8";
/*main container*/
#container {
    width:100%; margin:0 auto; padding:em(107) em(35) em(35);
    background:url(../../images/main/main_bg.jpg) 50% em(107) no-repeat; background-size:cover;
    &.on {height:100vh;overflow:hidden;}
    @include boxSizing;
    @extend %clearfix;
    .img_3D_wrap{
        position:relative; padding:em(150) 0 0;
        .goto3d_img { width:100%; }
        .btn_wrap {
            padding:em(45) 0 0; display:flex; justify-content:center;
            @extend %clearfix;
            @include boxSizing;
            button {
                width:em(338); height:em(100); font-size:em(26); font-weight:$bold; float:left;
                display:flex; align-items:center; justify-content:center;
                padding:em(35) 0 em(25);
                @include borderSet(em(2), solid);
                @include boxSizing;
                @include transition(border-color, .2s);
                @include boxShadow(0.1rem 0.3rem 0.5rem #1c1c1c8c);
                &:first-of-type {margin:0 em(20) 0 0;}
                img { width:20%; margin:0 em(10) 0 0; }
            }
            .btn_3D {
                color:$white; border-color:#393939; background-color:#393939;
            }
            .btn_simulate,
            .btn_gallery_popup {
                color:#292929; border-color:$white; background-color:$white;
            }
            .btn_gallery_popup {display:none;}
        }
    }
    //제품 카테고리별 그라데이션
    @mixin gradient_dot($color1, $color2){
        .model_title {background: linear-gradient(to right, $color1, $color2 30%);}
        .model_size_btn_active:after {background: linear-gradient(to right, $color1, $color2);}
        .key_features .hash_usp .hash_usp_btn_wrap button:before {background: linear-gradient(to bottom, $color1, $color2 50%);}
    }
    .model_info {
        width:100%; position:relative; top:50%; margin:em(60) 0 0; padding:em(35); background-color:rgba(255,255,255,.5);
        @include boxSizing;
        @include boxShadow(0px 5px 25px -14px $black);

        //카테고리별 색상 적용
        &.OLED_Color_dot {
            @include gradient_dot(#ff5454, #881c8a);
        }
        &.QNED_Color_dot {
            @include gradient_dot(#d564b9, #4d99be);
        }
        &.NanoCell_Color_dot {
            @include gradient_dot(#0c5e88, #25c190);
        }
        &.UHD_Color_dot {
            @include gradient_dot(#0c5e88, #25c190);
        }
        &.Lifestyle_Color_dot {
            @include gradient_dot(#d564b9, #4d99be);
        }
        &.Soundbar_Color_dot {
            @include gradient_dot(#ff7777, #6f4ada);
        }
        &.Portable_Color_dot {
            @include gradient_dot(#ed42fc, #2291cc);
        }
        &.Gaming_Speaker_Color_dot {
            @include gradient_dot(#fc377d, #ec973f);
        }
        &.Earbuds_Color_dot {
            @include gradient_dot(#9540fd, #34ba9f);
        }
        //카테고리별 색상 적용 end
        .model_name {
            font-size:em(54); font-weight:$medium;
            .model_title {
                line-height:em(35); display:block; font-size:em(30); padding:0 0 em(30); -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                img {width:35%;}
            }
        }
        .model_size_btn {
            width:min-content; display:flex; margin:em(40) 0 0; padding:0 0 em(20); position:relative; border-bottom:em(1) solid #e5e5e5;
            @extend %clearfix;
            &:before {width:100%; height:1px; display:block; content:""; position:absolute; bottom:0; left:0; background-color:#ccc;}
            button {
                display:block; min-width:em(100); font-size:em(40); font-weight:$bold; color:#959595;
                float:left; position:relative; line-height:em(45);
                &:after {
                    width:100%; height:em(7); display:block; content:""; background-color:#ccc;
                    position:absolute; bottom:em(-24);
                    @include boxShadow(em(1) em(1) em(5) em(-5) #000);
                    @include borderRadius(em(10));
                    @include opacity(0);
                    @include transition(opacity, .3s);
                }
            }
            .model_size_btn_active {
                color:$gray2;
                &:after {@include opacity(1)}
            }
            .model_size_btn_inactive {color:#cdcdcd;}
            .av_size_btn { width:em(160); padding:em(15) 0 em(5); }
            .av_size_none { display:none; }
            .av_white { background-color:$white !important; color:$black; border:em(2) solid #d6d6d6; }
            .av_black { background-color:$black !important; }
            .av_beige { background-color:#eae4dd !important; color:$black; }
            .av_burgundy { background-color:#450015 !important; }
            .av_charcoal_black { background-color:$black !important; }
            .av_peacock_green { background-color:#1b3247 !important; }
            .av_haze_gold { background-color:#bdaf9c !important; }
            .av_pearl_white { background-color:$white !important; color:$black; border:2px solid #d6d6d6; }
        }
        .art90_color_btn_wrap{
            margin:30px 0 0; display:inline-block; padding:0 0 10px; position:relative; border-bottom:1px solid #e5e5e5;
            @extend %clearfix;
            &:before {width:100%; height:1px; display:block; content:""; position:absolute; bottom:0; left:0; background-color:#ccc;}
            button {
                width:100px; font-size:16px; font-weight:$bold; color:#111; float:left; position:relative; line-height:20px;
                &:after {
                    width:100%; height:7px; display:block; content:""; background-color:#ccc; position:absolute; bottom:-13px;
                    @include boxShadow(1px 1px 5px -2px #000);
                    @include borderRadius(3px);
                    @include opacity(0);
                    @include transition(opacity, .3s);
                }
                &.model_size_btn_active:after {
                    background: linear-gradient(to right, #9434e5, #d08246);
                    @include opacity(1);
                }
                &:nth-of-type(1):before {background-color:#ccc4b9;}
                &:nth-of-type(2):before {background-color:#505948;}
                &:nth-of-type(3):before {background-color:#704b43;}
                &:before {
                    width:15px; height:15px; display:block; content:""; border:1px solid $white;
                    position:absolute; top:65px; left:50%;
                    @include translate(-50%, 0);
                    @include boxSizing;
                    @include borderRadius(100%);
                }
            }
        }
        .key_features {
            margin:em(70) 0 0;
            .title {position:relative; font-size:em(40); font-weight:$bold; line-height:em(45);}
            .hash_usp {
                margin:20px 0 0;
                .hash_usp_btn_wrap {
                    display:none;
                    button {
                        width:100%; height:em(100); display:none; font-size:em(34); font-weight:$bold;
                        padding:em(33) 0 em(27);
                        text-align:left; position:relative;background-image:url(../../images/main/key_feature_btn_bg.png);
                        border-bottom:em(1) solid $white; color:#707171; line-height:em(40); letter-spacing:em(-1);
                        @include boxSizing;
                        @include backgroundSoltC(0, 50%, no-repeat, 0 em(120));
                        @include transition(all, .4s);
                        &:before {
                            width:5px; height:0; display:block; content:""; position:absolute; top:0; left:0;
                            @include transition(height, .2s);
                        }
                        &:after {
                            width:100%; height:1px; position:absolute; bottom:1px; left:0; display:block; content:""; background-color:#cdcdce;
                            @include transition(opacity, .2s);
                        }
                    }
                }
                .hash_usp_btn_wrap_av{
                    display:none;
                    button {
                        width:100%; display:none; font-size:em(34); font-weight:$bold; padding:em(33) 0 em(27);
                        text-align:left; position:relative;background-image:url(../../images/main/key_feature_btn_bg.png);
                        border-bottom:em(1) solid $white; color:#707171; line-height:em(40); letter-spacing:em(-1);
                        @include boxSizing;
                        @include backgroundSoltC(0, 50%, no-repeat, 0 em(120));
                        @include transition(all, .4s);
                        &:before {
                            width:5px; height:0; display:block; content:""; position:absolute; top:0; left:0;
                            @include transition(height, .2s);
                        }
                        &:after {
                            width:100%; height:1px; position:absolute; bottom:1px; left:0; display:block; content:""; background-color:#cdcdce;
                            @include transition(opacity, .2s);
                        }
                    }
                    #spotify_hifi,
                    #virtual_dolby_atmos,
                    #wireless_subwoofer {
                        cursor:initial;
                        &:hover {
                            color:$black; background-color:transparent; border-radius:20px; font-weight:$regular;
                            &:before {background-color:$black;}
                        }
                    }
                }
            }
            .hash_sub {
                display:none; color:#3b3b3b; margin-top:18px;
                button {
                    height:26px; display:none; margin:0 0 8px 0; padding:2px 10px; font-size:18px; font-weight:$regular;
                    &:hover { color:$white; background-color:#df226b; border-radius:20px; font-weight:$regular; }
                }
            }
        }
        .dimension {
            padding:em(90) 0 0;
            .title {
                position:relative; font-size:em(40); font-weight:$bold; color:#727272; margin:0 0 em(30);
                .millimeter { display:inline-block; margin-left:em(10); font-size:em(20); color:#929292; }
            }
            .description_wrap{
                .description {
                    color:#6e6e6e; font-size:em(24); font-weight:$bold; margin:em(10) 0 0; line-height:em(30);
                    .hyphen { display:inline-block; letter-spacing:em(6); vertical-align:baseline; }
                }
            }
        }
        .ex_description {padding:em(35) 0 0; font-size:em(19); color:#727272; font-weight:$bold; line-height:em(27);}
    }
    //hashtag popup
    .hash_popup {
        width:100%; height:100%; display:none; position:absolute; left:0; top:em(108); background-color:$white;
        z-index:10;
        .popup_subusp_btn {
            background-color:$white;
            > button {width:100%; font-size:em(40); line-height:em(60); padding:em(25) 0 0; font-weight:$medium; color:$black; }
            .popup_sub_usp_wrap {
                padding:0 em(70); margin:em(25) 0 em(24); align-items:center;
                .popup_sub_usp {
                    width:100%; padding:0 0 0 em(30); font-size:em(24); line-height:em(58); margin:0 0 em(17) 0; text-align:left;
                    border:em(2) solid $gray3; color:#999; font-weight:$medium; box-sizing:border-box; position:relative;
                }
                .popup_sub_usp.active {
                    color:$gray2; background-color:$gray3; border-left-color:$gray3;
                    &:after {width:em(5); height:100%; content:""; background-color:$gray2; position:absolute; top:0; left:0;}
                }
            }
        }
        .popup_window {
            display:none; background-color:$white;
            @extend %clearfix;
            .hash_info_wrap{
                display:none;
                .hash_info {
                    padding:em(45) em(70) 0; text-align:center; margin:0 0 em(40);
                    .title {
                        font-size:em(52); line-height:em(62); font-weight:$bold; margin-bottom:em(20); color:$gray4;
                        .hide_title { display:none; }
                    }
                    .description { font-size:em(28); font-weight:$regular; line-height:em(50); color:$gray4; }
                }
                & > img { width:100% }
                .right_contents {
                    img { width:100%; }
                }
                .hash_frame {
                    position:relative; padding:0 0 56.25%;
                    iframe {position:absolute; top:0; left:0; z-index:1;}
                }
                .infinite_contrast_disclaimer { color:#757575; font-size:em(16); line-height:em(30); font-weight:$regular; padding:0 em(70); margin:em(40) 0;}
                .popup_disclaimer01 { color:#757575; font-size:em(16); line-height:em(30); font-weight:$regular; padding:0 em(70); margin:em(40) 0;}
                video { width:100%;}
            }
            .popup_close_btn {
                width:em(80); height:em(60); position:absolute; top:em(-83); left:em(15);
                &:before, &:after {content:""; width:30%; height:em(5); background-color:#333; position:absolute; left:em(28);}
                &:before {@include rotate(-45); top:em(20)}
                &:after {@include rotate(45); bottom:em(20)}
            }
            &.popup_window_av { height:800px; }
        }
    }

    //3d popup
    .webgl_wrap {
        width:100vw; height:calc(100% - 5.35rem); top:em(107); left:0; position:fixed; z-index:9; display:none;
        &:after {width:100%; height:100%; display:block; content:""; position:fixed; top:0; left:0; background-color:rgba(0,0,0,.6);}

    }
    #root {width:100%; height:100%; position:fixed;z-index:100; overflow:hidden;}

    //simulator popup
    .simulator_popup {
        width:100vw; height:calc(100% - 5.35rem); top:em(107); left:0; position:fixed; z-index:10; background-color:#111; display:none;
        @include opacity(0);
        .simulator_wrap {
            width:100%; height:100%; position:absolute; top:0; left:0; overflow-y:auto;
        }
        .simulator_bg {width:100%;}
        .simulator_controller {
            position:relative;
            .controller_box {background-color:#111;}
            .interior_box {
                margin:0 em(60) em(20);
                strong {text-align:center; color:$white; display:block; font-size:em(34); font-weight:$medium; border-bottom:1px solid #333; padding:em(30) 0 em(25)}
                ol {padding:em(25) em(40) 0; overflow:hidden;}
                li {
                    float:left; width:50%; box-sizing:border-box;
                    &:nth-of-type(odd){clear:both; padding:0 em(30) 0 0}
                    &:nth-of-type(even){float:right; padding:0 0 0 em(30)}
                }
                button {
                    @extend %clearfix;
                    &.active {
                        .img_wrap:after {@include opacity(1);}
                    }
                }
                .img_wrap {
                    overflow:hidden; position:relative;
                    &:after {
                        width:100%; height:100%; display:block; content:""; position:absolute; top:0; left:0; border:em(6) solid #bf9245;
                        @include boxSizing;
                        @include opacity(0);
                        @include transition(opacity, .2s);
                    }
                    img {width:100%;}
                }
                p {color:$white; font-size:em(24); font-weight:$regular; padding:em(15) 0}
            }
            .infill_on_off, .sound_on_off {
                padding:em(28) em(100); background-color:#1c1c1c;
                @extend %clearfix;
                p {font-size:em(28); font-weight:$medium; float:left; color:$white;}
                button {
                    width:em(92); height:em(37); background-color:#75716a; position:relative; float:right; border-radius:em(20);
                    @include transition(background-color, .2s);
                    &.active {background-color:#bf9245;}
                    &.active:after {transform:translateX(110%)}
                    &:after {
                        width:em(44); height:em(44); display:block; content:""; position:absolute; left:0; top:em(-3); background-color:$white;
                        @include borderRadius(100%);
                        @include transition;
                    }
                }
            }
            .installation_box {
                background-color:#262626; padding:0 em(90);
                strong {color:$white; display:block; font-size:20px; font-weight:$medium; border-bottom:1px solid #333; padding:10px 0 10px 14px;}
                .installation_btn_wrap {
                    display:flex;
                    @extend %clearfix;
                    button {
                        width:100%; color:#606162; font-size:em(22); padding:em(120) 0 em(30); position:relative; margin:0 em(10);
                        &:after {
                            content:""; width:em(101); height:em(78); position:absolute; top:em(22); left:50%;
                            @include translate(-50%, 0);
                            @include backgroundSoltC();
                        }
                        @include transition(background-color, .2s);
                        &.wide_stand_btn:after {background-image:url(../../images/main/simulator/wide_stand_icon.png);}
                        &.wall_mount_btn:after {background-image:url(../../images/main/simulator/wall_mount_icon.png);}
                        &.stand_btn:after {background-image:url(../../images/main/simulator/stand_icon.png)}
                        &.active {
                            color:#bf9245;
                            &.wide_stand_btn:after{background-image:url(../../images/main/simulator/wide_stand_icon_on.png);}
                            &.wall_mount_btn:after{background-image:url(../../images/main/simulator/wall_mount_icon_on.png);}
                            &.stand_btn:after{background-image:url(../../images/main/simulator/stand_icon_on.png);}
                        }
                    }
                }
            }
            .ok_btn_wrap {
                padding:0 14px 20px 14px;
                .simulator_ok_btn {
                    width:100%; height:52px; border:4px solid #bf9245; font-size:22px; color:#bf9245; font-weight:$bold;
                    @include boxSizing;
                    @include transition;
                    &:hover {background-color:#bf9245; color:$white;}
                }
            }
            .simulate_down_wrap {
                position:absolute; top:em(-80); right:em(20);
                @extend %clearfix;
                button {
                    width:em(62); height:em(62); color:$white; text-indent:-99999px; background-image:url(../../images/main/simulator/image_down_icon.png); border-radius:em(5);
                    @include backgroundSoltC();
                    @include boxShadow(4px 4px 6px rgba(0,0,0,0.35));
                }
            }
        }
    }
}

