/*filter*/
.filter_container{
    width:100%; height:100%; position:absolute; top:115px; left:0; background-color:$white;
    .filter_wrap {width:1300px; margin:48px auto 0;}
    .filter_title {
        margin:0 0 25px;
        strong {
            position:relative; font-size:30px; padding:0 0 15px 20px; display:block; border-bottom:1px solid #ccc;
            &:before {
                width:6px; height:6px; display:block; content:""; position:absolute; left:0; top:19px; background-color:$black;
                @include borderRadius(100%);
            }
        }
        .filter_checkbox {
            margin:9px 0 0; background-color:#f6f6f6; padding:15px 30px;
            @extend %clearfix;
            li {
                float:left; margin:0 0 0 100px; position:relative;
                &:first-child {margin:0;}
                &:nth-child(8) {margin:0; margin-top:30px;}
                &:nth-child(9), &:nth-child(10), &:nth-child(11) { margin-top:30px; }
                input[type="checkbox"] {
                    width:1px; height:1px; overflow:hidden;
                    &:checked + label:after {@include opacity(1)}
                }
                label {
                    padding:0 0 0 27px; font-size:20px; color:$black;
                    &:before {
                        width:18px; height:18px; display:block; content:""; border:2px solid #ddd; position:absolute; left:0; top:4px;
                        @include boxSizing;
                    }
                    &:after {
                        width:16px; height:15px; display:block; content:""; background:url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/main/checked_dot.png") 50% 50% no-repeat; position:absolute; left:4px; top:2px;
                        @include opacity(0);
                    }
                }
            }
        }
    }
    .filter_apply_btn {
        width:158px; height:48px; color:$white; background-color:$black; float:right; font-size:16px;
        @include borderRadius(24px);
    }
}

//product_profile
.product_profile_contents { width:1600px; height:100%; position:absolute; top:80px; left:50%; transform:translateX(-50%); background-color:$white; color:#545454; }
.product_profile_contents .tab { margin-bottom:40px; }
.product_profile_contents .tab:after { width:100%; height:2px; display:inline-block; background-color:#f0f0f0; content:''; }
.product_profile_contents .tab_oled { width:50%; position:relative; padding:7px 0 0; background-color:$white; font-size:26px; font-weight:600; color:#545454; }
.product_profile_contents .tab_oled.active:after { width:100%; height:4px; display:inline-block; position:absolute; top:50px; left:0; background-color:#e0236b; content:'';}
.product_profile_contents .tab_lcd { width:50%; position:relative; padding:7px 0 0; float:right; background-color:$white; font-size:26px; font-weight:600; color:#545454; }
.product_profile_contents .tab_lcd.active:after { width:100%; height:4px; display:inline-block; position:absolute; top:50px; left:0; background-color:#e0236b; content:'';}

.oled_img_wrap,
.lcd_img_wrap {
    width:1608px; height:700px; overflow-y:scroll; overflow-x:hidden;
    .detail_contents {
        display:none; position:relative;
        &:first-child {display:block;}
    }
    .detail_category {position:fixed; top:95px; left:0;}
    .detail_list {padding:133px 0 0;}
}

.oled_img_wrap::-webkit-scrollbar { display:block; width:6px; margin-left:10px; background-color:$white; }
.oled_img_wrap::-webkit-scrollbar-thumb { width:3px; background-color:#464646; border-radius: 10px; }
.oled_img_wrap::-webkit-scrollbar-track { width:3px; background-color:$white; border-radius: 10px; }
.lcd_img_wrap::-webkit-scrollbar { display:block; width:6px; margin-left:10px; background-color:$white; }
.lcd_img_wrap::-webkit-scrollbar-thumb { width:3px; background-color:#464646; border-radius: 10px; }
.lcd_img_wrap::-webkit-scrollbar-track { width:3px; background-color:$white; border-radius: 10px; }

.lcd_img_container { display:none; }
.product_profile_contents .oled_next_btn,
.product_profile_contents .lcd_next_btn { position:absolute; right:-100px; top:50%; transform:translateY(-124%); }
.product_profile_contents .oled_prev_btn,
.product_profile_contents .lcd_prev_btn { position:absolute; left:-100px; top:50%; transform:translateY(-124%); }


/*video Gallery*/
html { height:100%; }
body { height:100%; overflow:hidden; margin:0; padding:0; -ms-overflow-style: none; }
button { padding:0; border:0; outline:none; cursor:pointer; font-weight:500; }
//.gnb_container { height:86px; }
.gnb_container .gnb_wrap { max-width:1600px; height:86px; position:relative; margin:0 auto; }
.gnb_container .gnb_wrap h1 {padding:24px 0 0; float:left;}
.gnb_container .gnb_wrap .logo{ width:114px; height:56px; display:inline-block; background:url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/layout/logo.png") no-repeat; }
.gnb_container .gnb_wrap .title { display:inline-block; position:absolute; left:50%; top:32px; transform:translateX(-50%); font-size:40px; font-weight:600; }
.gnb_container .gnb_wrap .product_experience_btn { float:right; padding:12px; margin:36px 0 0; transition:all .1s; color:#616161; font-size:15px; font-weight:600; border: 2px solid #616161; border-radius:5px; }
.gnb_container .gnb_wrap .product_experience_btn:hover { background-color:#616161; color:$white; }

.user_info { margin:10px auto 0 50px; position:absolute; top:50%; right:50%; transform:translate(623px, -400px); }
.user_email, .connect_time { font-size:18px; font-weight:600; }
.select_video {width:1100px; height:100%; position:absolute; top:175px; left:50%; margin:0 0 0 -800px; text-align:center; z-index:10;}
.select_video .iframe_wrap { width:100%; position:relative; padding-bottom:56.25%; }
.select_video .video_iframe { width:100%; height:100%; position:absolute; transform:translateX(-50%); }
.select_video .title { text-align: left; position:absolute; bottom:-54px; }
.select_video .title_01 { width:40px; height:10px; margin:0 0 8px 0; padding:1px 8px; border-radius:6px; color:$white; font-size:20px; font-weight:600; background-color:#a4a5a6; }
.select_video .title_02 { font-size:19.6px; font-weight:600; }
.slide_show_btn { position:absolute; bottom:-60px; right:-52px; padding:6px 20px; margin:0 50px 0 0; line-height:20px; color:#616161; font-size:12px;  border:1px solid #616161; border-radius:6px; transition:all .1s; }
.slide_show_btn:hover { color:$white; background-color:#616161; }
.slide_show_btn img { display:inline-block; margin-right:10px; }

.slide_show_popup { width:100%; height:100%; display:none; position:absolute; top:0; left:0; background-color:rgba(0,0,0,0.8); z-index:999; }
.slide_show_popup .slide_show_wrap { width:1210px; position:relative; left:50%; top:50%; transform:translate(-50%, -50%); }
.slide_show_popup .slide_show_wrap iframe { width:1208px; height:716px; }
.slide_show_popup .slide_show_wrap .close_btn { position:absolute; top:-48px; right:-80px; font-size:25px; color:$white; }

.product_profile_contents { width:1600px; height:100%; position:absolute; top:80px; left:50%; transform:translateX(-50%); background-color:$white; color:#545454; }
.product_profile_contents .tab:after { width:100%; height:2px; display:inline-block; background-color:#f0f0f0; content:''; }
.product_profile_contents .tab_tv { width:50%; position:relative; padding:7px 0 0; background-color:$white; font-size:26px; font-weight:600; color:#545454; }
.product_profile_contents .tab_tv.active:after { width:100%; height:4px; display:inline-block; position:absolute; top:50px; left:0; background-color:#e0236b; content:'';}
.product_profile_contents .tab_av { width:50%; position:relative; padding:7px 0 0; float:right; background-color:$white; font-size:26px; font-weight:600; color:#545454; }
.product_profile_contents .tab_av.active:after { width:100%; height:4px; display:inline-block; position:absolute; top:50px; left:0; background-color:#e0236b; content:'';}

.product_profile_contents .tab_tv_contents { width:440px; height:calc(100% - 221px); position:absolute; top:95px; right:0; background-color:#f8f8f8; overflow-y:scroll; }
.product_profile_contents .tab_tv_contents li:first-child { margin:25px 0 0 0; }
.product_profile_contents .tab_tv_contents li { width:100%; cursor:pointer; position:relative; }
.product_profile_contents .tab_tv_contents li.active:before { width:8px; height:100%; position:absolute; display:block; content:''; background-color:#e0236b;}
.product_profile_contents .tab_tv_contents li .contents_wrap { padding:8px 21px; box-sizing:border-box; }
.product_profile_contents .tab_tv_contents li.active .contents_wrap { background-color:#e3e4e5; }
.product_profile_contents .tab_tv_contents li img { width:132px; height:74px; background-size:cover; }
.product_profile_contents .tab_tv_contents li a { width:350px; height:80px; display:block; }
.product_profile_contents .tab_tv_contents li a:after { display:block; content:''; clear:both; }
.product_profile_contents .tab_tv_contents .title { width:244px; float:right; }
.product_profile_contents .tab_tv_contents .title_01 { display:inline-block; margin:0 0 8px 0; padding:2px 4px; letter-spacing:0.5px; border-radius:2px; color:$white; font-size:13px; font-weight:600; background-color:#a4a5a6; }
.product_profile_contents .tab_tv_contents .title_02 { display:block; font-size:16px; font-weight:600; clear:both; }

.product_profile_contents .tab_av_contents { width:440px; height:calc(100% - 221px); position:absolute; top:95px; right:0; background-color:#f8f8f8; overflow-y:scroll; }
.product_profile_contents .tab_av_contents .active { z-index:1; }
.product_profile_contents .tab_av_contents li:first-child { margin:25px 0 0 0; }
.product_profile_contents .tab_av_contents li { width:100%; cursor:pointer; position:relative; }
.product_profile_contents .tab_av_contents li.active:before { width:8px; height:100%; position:absolute; display:block; content:''; background-color:#e0236b;}
.product_profile_contents .tab_av_contents li .contents_wrap { padding:8px 21px; box-sizing:border-box; }
.product_profile_contents .tab_av_contents li.active .contents_wrap { background-color:#e3e4e5; }
.product_profile_contents .tab_av_contents li img { width:132px; height:74px; }
.product_profile_contents .tab_av_contents li a { width:350px; height:80px; display:block; }
.product_profile_contents .tab_av_contents li a:after { display:block; content:''; clear:both; }
.product_profile_contents .tab_av_contents .title { width:244px; float:right; }
.product_profile_contents .tab_av_contents .title_01 { display:inline-block; margin:0 0 8px 0; padding:2px 4px; letter-spacing:0.5px; border-radius:2px; color:$white; font-size:13px; font-weight:600; background-color:#a4a5a6; }
.product_profile_contents .tab_av_contents .title_02 { display:block; font-size:16px; font-weight:600; clear:both; }

/* 211217 start*/
.studio_container {
    width:100%; height:100vh; background:url(../../images/main/studio_bg.jpg) 50% 100% no-repeat;
    background-size:cover; overflow:hidden;
    h1 {
        margin:15vw auto 0; font-size:em(68);color:$white;font-family:$LGInnovation; line-height:em(74);
        text-align:center;text-shadow:0 0 em(5) #583c31, 0 0 em(8) #745141, 0 0 em(10) #705543;
    }
    ul.link_wrap {
        width:100%; position:absolute; bottom:12%;padding:0 em(25);
        @include boxSizing;
        li {
            display:block; position:absolute;
            @include transform(translate(-50%,0));
            &:nth-of-type(1) {width:24.21vw; top:-11vw;left:50%;margin:0 0 0 -35vw;}
            &:nth-of-type(2) {
                width:36.06vw;left:50%;
                a {width:em(216);}
            }
            &:nth-of-type(3) {width:25.13vw; top:-11vw;left:50%;margin:0 0 0 35vw;}
            img { width:100%; }
            a {
                width:em(173);margin:em(20) auto 0;display:block;font-size:em(18);padding:em(15) 0 em(10);
                text-align:center;color:$white; font-weight:$bold;
                text-shadow:0 0 em(30) $black,0 0 em(30) $black;
                border:em(3) solid $white;
                @include boxSizing;
            }
        }
    }
}
/* 211217 end*/



















